hr {
  overflow: visible;
  /* For IE */
  height: 30px;
  border-style: solid;
  border-color: #d19a2d;
  border-width: 1px 0 0 0;
  border-radius: 20px;
  width: 50%; }

hr:before {
  /* Not really supposed to work, but does */
  display: block;
  content: "";
  height: 30px;
  margin-top: -31px;
  border-style: solid;
  border-color: #d19a2d;
  border-width: 0 0 1px 0;
  border-radius: 20px; }

